html {
  font-size: 100%;
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  padding: 0;
  min-height: 200vh;
  overflow-x: hidden;
  font-family: "Public Sans", sans-serif;
  font-size: 1.125rem;
  line-height: 1.3;
  font-weight: 300;
  color: hsl(233, 8%, 62%);
}
body.noscroll {
  overflow: hidden;
}
@media (min-width: 71.875em) {
  body {
    font-size: 1.125rem;
  }
}

h1,
h2,
h3 {
  margin-top: 0px;
  font-weight: 300;
  line-height: 1.15;
  color: hsl(233, 26%, 24%);
}

p {
  margin-bottom: 2.25rem;
  line-height: 1.5;
}

h1 {
  margin-bottom: 1.5rem;
  font-size: 2.3125rem;
}
@media (min-width: 71.875em) {
  h1 {
    font-size: 3.25rem;
  }
}

h2 {
  font-size: 1.875rem;
  margin-bottom: 1.5625rem;
}
@media (min-width: 71.875em) {
  h2 {
    font-size: 2.25rem;
    margin-bottom: 2.25rem;
  }
}

a,
a:visited,
a:hover {
  text-decoration: none;
}

.flex {
  display: flex;
}
.flex-jc-sb {
  justify-content: space-between;
}
.flex-jc-c {
  justify-content: center;
}
.flex-ai-c {
  align-items: center;
}

button,
.button {
  position: relative;
  display: inline-block;
  padding: 0.875rem 2.1875rem;
  background-image: linear-gradient(to right, hsl(136, 65%, 51%), hsl(192, 70%, 51%));
  border: 0;
  border-radius: 3.125rem;
  cursor: pointer;
  color: hsl(0, 0%, 100%);
  font-weight: 400;
  font-size: 0.875rem;
  transition: opacity 300ms ease-in-out;
  overflow: hidden;
}
button::before,
.button::before {
  content: "";
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  background-color: rgba(255, 255, 255, 0.25);
  opacity: 0;
  transition: opacity 150ms ease-in-out;
}
button:hover::before,
.button:hover::before {
  opacity: 1;
}

@media (max-width: 71.8125em) {
  .hide-for-mobile {
    display: none;
  }
}

@media (min-width: 71.875em) {
  .hide-for-desktop {
    display: none;
  }
}

.container {
  max-width: 69.5625rem;
  margin: 0 auto;
}
.container--pall {
  padding-right: 1.5rem;
  padding-left: 1.5rem;
  padding-top: 4.375rem;
  padding-bottom: 4.375rem;
}
@media (min-width: 71.875em) {
  .container--pall {
    padding-top: 6rem;
    padding-bottom: 6rem;
  }
}
.container--px {
  padding-right: 1.5rem;
  padding-left: 1.5rem;
}
.container--py {
  padding-top: 4.375rem;
  padding-bottom: 4.375rem;
}
@media (min-width: 71.875em) {
  .container--py {
    padding-top: 6rem;
    padding-bottom: 6rem;
  }
}
.container--pt {
  padding-top: 4.375rem;
}
@media (min-width: 71.875em) {
  .container--pt {
    padding-top: 6rem;
  }
}
.container--pr {
  padding-right: 1.5rem;
}
.container--pb {
  padding-bottom: 4.375rem;
}
@media (min-width: 71.875em) {
  .container--pb {
    padding-bottom: 6rem;
  }
}
.container--pl {
  padding-left: 1.5rem;
}

@keyframes fade-in {
  from {
    visibility: hidden;
    opacity: 0;
  }
  1% {
    visibility: visible;
    opacity: 0;
  }
  to {
    visibility: visible;
    opacity: 1;
  }
}
.fade-in {
  animation: fade-in 200ms ease-in-out forwards;
}

@keyframes fade-out {
  from {
    visibility: visible;
    opacity: 1;
  }
  99% {
    visibility: visible;
    opacity: 0;
  }
  to {
    visibility: hidden;
    opacity: 0;
  }
}
.fade-out {
  animation: fade-out 200ms ease-in-out forwards;
}

.header nav {
  position: relative;
  background-color: hsl(0, 0%, 100%);
  padding-top: 1.0625rem;
  padding-bottom: 1.0625rem;
}
.header__logo img {
  width: 8.8125rem;
  height: 1.375rem;
}
.header__menu > span {
  display: block;
  width: 1.625rem;
  height: 0.125rem;
  background-color: hsl(233, 26%, 24%);
  transition: all 300ms ease-in-out;
  transform-origin: 0.1875rem 0.0625rem;
}
.header__menu > span:not(:last-child) {
  margin-bottom: 0.3125rem;
}
@media (max-width: 71.8125em) {
  .header__links {
    position: absolute;
    top: 6.25rem;
    width: calc(100% - 3rem);
    left: 50%;
    transform: translateX(-50%);
    border-radius: 0.3125rem;
    background-color: hsl(0, 0%, 100%);
  }
  .header__links a {
    text-align: center;
    line-height: 2.5;
    display: block;
    width: 100%;
    font-size: 1.125rem;
    color: hsl(233, 26%, 24%);
  }
  .header__links a:first-child {
    padding-top: 1.25rem;
  }
  .header__links a:last-child {
    padding-bottom: 1.25rem;
  }
}
@media (min-width: 71.875em) {
  .header__links a {
    position: relative;
    font-size: 0.875rem;
    transition: color 300ms ease-in-out;
    color: hsl(233, 8%, 62%);
  }
  .header__links a:not(:last-child) {
    margin-right: 2rem;
  }
  .header__links a::after {
    content: "";
    display: block;
    position: absolute;
    left: 0;
    right: 0;
    bottom: -1.875rem;
    width: 100%;
    height: 0.3125rem;
    background: linear-gradient(to right, hsl(136, 65%, 51%), hsl(192, 70%, 51%));
    opacity: 0;
    transition: opacity 300ms ease-in-out;
  }
  .header__links a:hover {
    color: hsl(233, 26%, 24%);
  }
  .header__links a:hover::after {
    opacity: 1;
  }
}
.header .overlay {
  opacity: 0;
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: -1;
  background-image: linear-gradient(hsl(233, 26%, 24%), transparent);
}
.header.open .overlay {
  display: block;
  top: 3.75rem;
  z-index: 5;
}
.header.open .header__menu {
  z-index: 6;
}
.header.open .header__menu > span:first-child {
  transform: rotate(45deg);
}
.header.open .header__menu > span:nth-child(2) {
  opacity: 0;
}
.header.open .header__menu > span:last-child {
  transform: rotate(-45deg);
}
.header.open .header__links {
  z-index: 6;
}

.hero {
  background-color: hsl(0, 0%, 98%);
}
@media (min-width: 71.875em) {
  .hero .container {
    display: flex;
    align-items: center;
  }
}
.hero__image {
  position: relative;
  min-height: 17.5rem;
  background-image: url("../images/bg-intro-mobile.svg");
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center center;
}
@media (min-width: 40em) {
  .hero__image {
    min-height: 25rem;
    background-position: center 70%;
  }
}
@media (min-width: 71.875em) {
  .hero__image {
    height: 40.9375rem;
    flex: 3;
    order: 2;
    background-image: none;
  }
}
@media (min-width: 71.875em) {
  .hero__image::before {
    content: "";
    position: absolute;
    height: 100%;
    width: 150%;
    background-image: url("/images/bg-intro-desktop.svg");
    background-repeat: no-repeat;
    background-size: 122%;
    background-position: 0% 83%;
  }
}
.hero__image::after {
  content: "";
  position: absolute;
  width: 100%;
  height: 100%;
  background-image: url("../images/image-mockups.png");
  background-size: 93%;
  background-repeat: no-repeat;
  background-position: center bottom;
}
@media (min-width: 40em) {
  .hero__image::after {
    background-size: 52%;
  }
}
@media (min-width: 71.875em) {
  .hero__image::after {
    width: 120%;
    height: 122%;
    background-size: 94%;
    background-position: center 150%;
    left: 22%;
  }
}
.hero__text {
  text-align: center;
}
@media (min-width: 71.875em) {
  .hero__text {
    text-align: left;
    flex: 2;
    order: 1;
  }
}

.feature {
  background-color: hsl(220, 16%, 96%);
  text-align: center;
}
@media (min-width: 40em) {
  .feature {
    text-align: left;
  }
}
.feature__intro {
  margin-bottom: 3.75rem;
}
@media (min-width: 71.875em) {
  .feature__intro {
    width: 60%;
  }
}
@media (min-width: 40em) {
  .feature__grid {
    display: flex;
    flex-wrap: wrap;
  }
}
.feature__item {
  padding: 1.875rem 1.875rem 1.875rem 0rem;
}
@media (min-width: 40em) {
  .feature__item {
    flex: 0 0 50%;
  }
}
@media (min-width: 71.875em) {
  .feature__item {
    flex: 1;
  }
}
.feature__icon {
  margin-bottom: 1.875rem;
}
@media (min-width: 71.875em) {
  .feature__icon {
    margin-bottom: 2.75rem;
  }
}
.feature__title {
  margin-bottom: 1rem;
  font-size: 1.25rem;
  font-weight: 300;
  line-height: 1.15;
  color: hsl(233, 26%, 24%);
}
@media (min-width: 71.875em) {
  .feature__title {
    margin-bottom: 1.5rem;
    font-size: 1.5rem;
  }
}
.feature__description {
  font-size: 0.875rem;
  line-height: 1.5;
}

.articles {
  background-color: hsl(0, 0%, 98%);
}

.article__grid {
  display: grid;
  gap: 1.875rem;
  grid-template-columns: 1fr;
}
@media (min-width: 40em) {
  .article__grid {
    grid-template-columns: repeat(2, 1fr);
  }
}
@media (min-width: 71.875em) {
  .article__grid {
    grid-template-columns: repeat(4, 1fr);
  }
}
.article__item {
  background-color: hsl(0, 0%, 100%);
  border-radius: 0.3125rem;
  overflow: hidden;
  box-shadow: 0px 0.1875rem 0.75rem rgba(0, 0, 0, 0.15);
  transition: all 150ms ease-in-out;
}
.article__item:hover {
  transform: scale(1.01);
}
.article__image {
  height: 12.5rem;
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center center;
}
.article__text {
  color: hsl(233, 8%, 62%);
  padding: 1.875rem 1.875rem 2.5rem 1.875rem;
}
@media (min-width: 40em) {
  .article__text {
    padding: 1.875rem 1.5625rem;
  }
}
.article__author {
  margin-bottom: 0.75rem;
  font-size: 0.625rem;
}
.article__title {
  margin-bottom: 0.5rem;
  font-size: 1.0625rem;
  line-height: 1.2;
  color: hsl(233, 26%, 24%);
}
.article__description {
  font-size: 0.8125rem;
}

.footer {
  padding: 2.5rem;
  text-align: center;
  background-color: hsl(233, 26%, 24%);
  color: hsl(0, 0%, 100%);
}
.footer .container {
  margin-bottom: 1.25rem;
}
@media (min-width: 71.875em) {
  .footer .container {
    display: grid;
    grid-template-columns: 2fr 1fr 1fr 2fr;
    grid-template-rows: 1fr auto;
    grid-template-areas: "logo links1 links2 cta" "social links1 links2 copyright";
    gap: 1rem;
    justify-items: start;
  }
}
.footer a {
  color: hsl(0, 0%, 100%);
}
.footer__logo {
  display: inline-block;
}
@media (max-width: 71.8125em) {
  .footer__logo {
    margin-bottom: 1.875rem;
  }
}
@media (min-width: 71.875em) {
  .footer__logo {
    grid-area: logo;
  }
}
@media (max-width: 71.8125em) {
  .footer__social {
    margin-bottom: 1.875rem;
  }
}
@media (min-width: 71.875em) {
  .footer__social {
    grid-area: social;
    align-self: end;
  }
}
.footer__social a {
  display: inline-block;
  height: 1.25rem;
}
.footer__social a svg path {
  transition: fill 150ms ease-in-out;
}
.footer__social a:hover svg path {
  fill: hsl(136, 65%, 51%);
}
.footer__social a:not(:last-child) {
  display: inline-block;
  margin-right: 1rem;
}
.footer__links {
  display: flex;
  flex-direction: column;
  align-items: center;
  font-size: 0.9375rem;
}
@media (min-width: 71.875em) {
  .footer__links {
    justify-content: space-between;
    align-items: start;
  }
}
@media (min-width: 71.875em) {
  .footer__links.col1 {
    align-items: start;
    grid-area: links1;
  }
}
@media (max-width: 71.8125em) {
  .footer__links.col2 {
    margin-bottom: 1.875rem;
  }
}
@media (min-width: 71.875em) {
  .footer__links.col2 {
    grid-area: links2;
  }
}
.footer__links a {
  line-height: 2.25;
  transition: color 150ms ease-in-out;
}
.footer__links a:hover {
  color: hsl(136, 65%, 51%);
}
@media (max-width: 71.8125em) {
  .footer__cta a.button {
    margin-bottom: 1.875rem;
  }
}
@media (min-width: 71.875em) {
  .footer__cta {
    grid-area: cta;
    text-align: right;
    justify-self: end;
  }
}
.footer__copyright {
  font-size: 0.9375rem;
  color: hsl(233, 8%, 62%);
}
@media (min-width: 71.875em) {
  .footer__copyright {
    grid-area: copyright;
    place-self: end;
  }
}
.footer .attribution {
  display: inline-block;
  font-size: 0.6875rem;
  text-align: center;
  color: hsl(233, 8%, 62%);
}
.footer .attribution a {
  color: hsl(228, 45%, 44%);
}